import axios from "axios";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import { List } from "../../assets";
import styles from "./Form.module.css";

export const Form = () => {
  const [name, setName] = useState("");
  const [number, setNumber] = useState();
  const [email, setEmail] = useState("");
  const [send, setSend] = useState(false);
  const { t } = useTranslation();

  const handlePost = async (e) => {
    e.preventDefault();

    const client = {
      fullname: name,
      phone_number: number,
    };
    await axios
      .post("https://begzod-farrux.uz/api/clients/", client, {
        headers: {
          Authorization:
            "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ0b2tlbl90eXBlIjoiYWNjZXNzIiwiZXhwIjo0ODQ3MzI3Mzk5LCJpYXQiOjE2OTM3MjczOTksImp0aSI6IjVmZGU2MjdkNzc0ZTRkMjg4NGI3ZDMzNTg0OGI3NjY1IiwidXNlcl9pZCI6MX0.Pxoacrz3QpnUpmLZcnyMzMiAJicz7Rm-M-qiMUA7vc4",
        },
      })
      .then((res) => {
        setSend(true);
        setNumber("");
        setName("");
        setTimeout(() => {
          setSend(false);
        }, 2500);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <form onSubmit={handlePost}>
      <div className="container">
        <div className={styles.form}>
          <img src={List} className={styles.List} alt="" />
          <div className={styles.data}>
            <label htmlFor="name">
              <span>{t("form.name")}</span>
              <input
                type="text"
                id="name"
                value={name}
                required
                onChange={(e) => setName(e.target.value)}
              />
            </label>
            <label htmlFor="number">
              <span>{t("form.number")}</span>
              <input
                type="number"
                id="number"
                value={number}
                required
                onChange={(e) => setNumber(e.target.value)}
              />
            </label>
            <label htmlFor="mail">
              <span>E-mail</span>
              <input
                type="email"
                id="mail"
                required
                placeholder="Необъязательно"
                onChange={(e) => setEmail(e.target.value)}
              />
            </label>
          </div>
          <button onClick={handlePost}>{send ? "✔" : t("send")}</button>
        </div>
      </div>
    </form>
  );
};
