import "./Home.css";
import { pulse } from "../../assets";
import { SlideInfo } from "../../constants";
import { Form, NewsCard, TovarCard } from "../../components";
import { useTranslation } from "react-i18next";
import { Suspense, useEffect, useState } from "react";
import axios from "axios";

export function Home() {
  const [tovar, setTovar] = useState([]);
  const getProducts = async () => {
    await axios
      .get("https://begzod-farrux.uz/api/products/")
      .then((res) => setTovar(res?.data))
      .catch((err) => console.log(err));
  };
  const [blog, setBlog] = useState([]);
  const getBlogs = async () => {
    await axios
      .get("https://begzod-farrux.uz/api/blogs/")
      .then((res) => setBlog(res?.data))
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    getProducts();
    getBlogs();
  }, []);
  const slideInfo = SlideInfo();
  const { t } = useTranslation();
  return (
    <main>
      <section className="swiperSection">
        <div className="container">
          <div className="slide">
            <img className="bg" src={slideInfo[0].bg} alt="" />
            <div className="info">
              {slideInfo[0].text}
              <img src={pulse} alt="" />
            </div>
          </div>
        </div>
      </section>
      <section className="form">
        <Form />
      </section>
      <div className="container">
        <section className="medicine">
          <h2 className="heading">{t("medicine")}</h2>
          <img src={pulse} className="headingImg" alt="" />
          <div className="tovars">
            <Suspense fallback={<h1>Loading...</h1>}>
              {tovar.map((tov) => (
                <TovarCard key={tov.id} data={tov} />
              ))}
            </Suspense>
          </div>
        </section>
        <section className="medicine">
          <h2 className="heading">{t("news")}</h2>
          <img src={pulse} className="headingImg" alt="" />
          <div className="news">
            <Suspense fallback={<h1>Loading...</h1>}>
              {blog.map((tov) => (
                <NewsCard key={tov.id} data={tov} />
              ))}
            </Suspense>
          </div>
        </section>
      </div>
    </main>
  );
}
