import { useEffect, useState } from "react";
import styles from "./Contact.module.css";
import GetMap from "../../components/map/MAP";
import { useTranslation } from "react-i18next";
import axios from "axios";

export const Contact = () => {
  const [name, setName] = useState("");
  const [number, setNumber] = useState();
  const [send, setSend] = useState(false);

  const { t } = useTranslation();
  const handlePost = async (e) => {
    e.preventDefault();

    const client = {
      fullname: name,
      phone_number: number,
    };
    await axios
      .post("https://begzod-farrux.uz/api/clients/", client, {
        headers: {
          Authorization:
            "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ0b2tlbl90eXBlIjoiYWNjZXNzIiwiZXhwIjo0ODQ3MjcwMTg2LCJpYXQiOjE2OTM2NzAxODYsImp0aSI6ImM3ZmQ3MzAxNTMyMjQyZTY5ZjdiMGJiMTY0NTA3MGQyIiwidXNlcl9pZCI6MX0.NLNqeq2FN3uCLaicAgvF2wZ2sbiZ-godNHl0HutvLvI",
        },
      })
      .then((res) => {
        setSend(true);
        setNumber("");
        setName("");
        setTimeout(() => {
          setSend(false);
        }, 2500);
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <main>
      <div className={styles.title}>
        <div className="container">
          <h1>{t("contact.title")}</h1>
        </div>
      </div>
      <div className="container">
        <div className={styles.contact}>
          <div className={styles.contactForm}>
            <form onSubmit={handlePost}>
              <h2>{t("contact.form")}</h2>
              <p>{t("contact.text")}</p>
              <label htmlFor="name">
                <input
                  type="text"
                  id="name"
                  value={name}
                  required
                  placeholder={t("placeholder.name")}
                  onChange={(e) => setName(e.target.value)}
                />
              </label>
              <label htmlFor="number">
                <input
                  type="number"
                  id="number"
                  value={number}
                  required
                  placeholder={t("placeholder.phone")}
                  onChange={(e) => setNumber(e.target.value)}
                />
              </label>
              <button onSubmit={handlePost}>{send ? "✔" : t("send")}</button>
            </form>
            <div className={styles.about}>
              <p>{t("info.title")}</p>
              <ul>
                <li>{t("info.item1")}</li>
                <li>{t("info.item2")}</li>
                <li>{t("info.item3")}</li>
                <li>{t("info.item4")}</li>
                <li>{t("info.item5")}</li>
                <li>{t("info.item6")}</li>
                <li>{t("info.item7")}</li>
              </ul>
            </div>
          </div>
          <div className={styles.headOffice}>
            <h2>{t("contact.office")}</h2>
            <h4>{t("contact.location")}</h4>
            <GetMap />
          </div>
        </div>
      </div>
    </main>
  );
};
