import { useTranslation } from "react-i18next";
import styles from "./TovarCard.module.css";

export const TovarCard = ({ data }) => {
  const { i18n } = useTranslation();
  return (
    <div className={styles.tovarCard}>
      <img
        src={data.image}
        alt=""
      />
      <h5>{i18n.language === "ru" ? data.name_ru : data.name_uz}</h5>
      <p>
        {i18n.language === "ru" ? data.description_ru : data.description_uz}
      </p>
    </div>
  );
};
